<template>
  <div>
    <LayoutBox one="店铺行业" two="行业列表">
      <el-form size="small" inline>
        <el-form-item>
          <el-button type="danger" icon="el-icon-plus" @click="dialogPvVisible = true">新增</el-button>
        </el-form-item>
      </el-form>

      <el-table :data="tableData" border size="small">
        <el-table-column prop="createTime" label="日期" align="center"></el-table-column>
        <el-table-column prop="tradeName" label="行业名称" align="center"></el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="scope">
            <el-button type="success" size="small" @click="onEdit(scope)">编辑</el-button>
            <el-button type="danger" size="small">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination class="mt-1 text-right" background :current-page="queryInit.pageNo" :page-sizes="[10, 20, 30, 40]" :page-size="queryInit.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="queryInit.total" @size-change="handleSizeChange" @current-change="handleCurrentChange" />
    </LayoutBox>

    <el-dialog :visible.sync="dialogPvVisible" :title="tradeForm.id ? '编辑' : '新增'" :before-close="resetForm">
      <el-form ref="tradeForm" :model="tradeForm" :rules="rules" size="small" label-width="80px">
        <el-form-item label="行业名称" prop="tradeName">
          <el-input v-model="tradeForm.tradeName"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button type="primary" size="small" @click="submitForm('tradeForm')">发布</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import { MIXINS_TRADEADDTRADE_API, MIXINS_TRADEQUERYLIST_API } from '@/mixins/api.js'
import { MIXINS_BASIC_FORMRULES } from '@/mixins/form.js'

export default {
  name: 'shopIndustryList',

  components: {
    LayoutBox,
  },

  mixins: [
    MIXINS_TRADEADDTRADE_API,
    MIXINS_BASIC_FORMRULES,
    MIXINS_TRADEQUERYLIST_API,
  ],

  data() {
    return {
      tradeForm: {
        id: '', // 编辑时的id
        tradeName: '', // 行业名称
      },
      rules: {
        tradeName: [
          { required: true, message: '请输入行业名称', trigger: 'blur' },
        ]
      },
      tableData: [],
      dialogPvVisible: false, // 新增框
      // 分页初始化
      queryInit: {
        pageNo: 1,
        pageSize: 10,
        total: 0,
      }
    }
  },

  methods: {
    // 点击编辑
    onEdit (scope) {
      this.tradeForm.id = scope.row.id
      this.dialogPvVisible = true
    },

    // 点击发布
    submitForm (formName) {
      if (this._ruleForm(formName)) {
        let data = {
          ...this.tradeForm
        }
        this.tradeAddTrade(data).then((res) => {
          console.log(res)
          if (res.data.success) {
            this.$message.success('提交成功')
            this.$refs.tradeForm.resetFields()
            this.dialogPvVisible = false
          } else {
            this.$message.error(res.data.msg)
          }
        })
      }
    },

    // 分页选择每页显示多少条
    handleSizeChange (pageSize) {
      this.queryInit.pageSize = pageSize
      this.apiTradeQueryList()
    },

    // 分页选择第几页
    handleCurrentChange (pageNo) {
      this.queryInit.pageNo = pageNo
      this.apiTradeQueryList()
    },

    // 行业查询（分页）
    apiTradeQueryList () {
      let data = {
        ...this.queryInit
      }
      this.tradeQueryList(data).then((res) => {
        if (res.data.success) {
          this.tableData = res.data.data
          this.queryInit = {
            total: res.data.data[0].count,
            ...res.data.data[0]
          }
        } else {
          this.$message.error(res.data.msg)
        }
      })
    },

    // 关闭Dialog
    resetForm () {
      this.$refs.tradeForm.resetFields()
      this.dialogPvVisible = false
    },
  },

  created () {
    this.apiTradeQueryList()
  }
}
</script>

<style lang="scss" scoped>

</style>
